                    a <strong>Snapshot Block Producer</strong> (SBP). These SBPs are responsible for adding Snapshot Blocks to the Vite's
                    Snapshot Chain, which acts as a confirmation for all transactions occuring on the Vite network.
                </p>
                <p> As SBPs create new blocks, they directly earn block rewards from the network. Moreover, the more votes an SBP gets
